<template>
    <form class="m-4 text-center" @submit.prevent="Load">
        <input type="text" placeholder="My todo" v-model="fill"/>
    <input type="submit" id="bt"/>
    </form>
    <div class="container-fluid">
        <div class="column d-flex justify-content-center">
            <div class="col-md-6">
  <table class="table table-bordered ">
  <thead>
    <tr>
      <th scope="col">Task</th>
      <th scope="col">Status</th>
      <th scope="col">Delete</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(tasks, index) in task" :key="index">
     <td>{{tasks.todo}}</td>
      <td>{{tasks.status}}</td>
      <td @click="Delete(index)"><i class="fa fa-trash"></i></td>
    </tr>
  </tbody>
</table>

            </div>
        </div>
    </div>
</template>

<script>
export default {
data(){
    return{
        fill:'',
        task:[
            {todo:'Pray to God', status:'To-do', id:1},
            {todo:'Brush', status:'To-do', id:2},
            {todo:'Meditate', status:'To-do', id:3},
            {todo:'Hit the Gym', status:'To-do', id:4},
        ]
    }
},
    methods:{
        Load(){
           if(this.fill.length===0){
               alert("Please fill in your todo")
           }
            else{
                 this.task.push({
                todo:this.fill,
                status:'To-do',
                id:Math.random(1000),
            })
            }
          this.fill='';
        },

        Delete(index){
            this.task.splice(index,1)
        }
    }
}
</script>

<style scoped>
    body{
        margin:auto 40px;
    }
   input{
       padding:5px;
   }
   table{
       padding:5px;
   }
   .fa{
       padding:10px;
   }
</style> 